import { useState, useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import { addSession } from '../utils/db';
import '../addSessionStyles.css';
import '../styles.css';

const splitNames = (text = '') =>
    text
        .split(/\r?\n|,|;|\t/g)
        .map((s) => s.trim())
        .filter(Boolean);

const todayStr = () => new Date().toISOString().slice(0, 10);

const AddSessionPage = () => {
    const [sessionName, setSessionName] = useState('');
    const [date, setDate] = useState(todayStr());
    const [participants, setParticipants] = useState('');
    const navigate = useNavigate();

    const parsed = useMemo(() => splitNames(participants), [participants]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!sessionName.trim()) return alert('  .');
        if (!date) return alert(' .');
        if (parsed.length === 0) return alert('  .');

        await addSession({ name: sessionName.trim(), date, participants: parsed });
        navigate('/');
    };

    return (
        <div className="add-session-page">
            <h1>  </h1>
            {/*  equal-container  */}
            <div className="container equal-container">
                <form onSubmit={handleSubmit}>
                    <label>
                        <div className="text"> </div>
                        <input
                            className="textarea"
                            type="text"
                            value={sessionName}
                            onChange={(e) => setSessionName(e.target.value)}
                            placeholder=": 14 "
                        />
                    </label>

                    <p />

                    <label>
                        <div className="text"></div>
                        <input
                            className="textarea"
                            type="date"
                            value={date}
                            onChange={(e) => setDate(e.target.value)}
                        />
                    </label>

                    <p />

                    <label>
                        <div className="text">  (/// )</div>
                        <textarea
                            className="textarea"
                            value={participants}
                            onChange={(e) => setParticipants(e.target.value)}
                            placeholder={': , '}
                            rows={8}
                        />
                        <div className="hint"> : {parsed.length}</div>
                    </label>

                    <div className="btn-row">
                        <button className="btn" type="submit">
                            
                        </button>
                        <button className="btn cancel" type="button" onClick={() => navigate('/')}>
                            
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
};

export default AddSessionPage;
